<template>
  <div id="deal">
    <div class="col-md-12">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title"></h3>
          <router-link to="/admin/record/deal" class="pull-right btn btn-primary" style="margin-left: 20px">
          </router-link>
          <a :href="'http://'+this.$data.url" download="" class="btn btn-primary" style="float: right;color: #fff"></a>
        </div>
        <div class="box-body">
          <!---->
          <ul>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.orderNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.merchantName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.proxyName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.proxyName1" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-date-picker
                v-model="date"
                type="daterange"
                align="right"
                placeholder=""
                :picker-options="pickerOptions2" size="small">
              </el-date-picker>
            </li>
            <li class="same">
              <label>:</label>
              <div class="form-control price">
                <input type="text" name="date" value="" v-model="query.lessTotalFee">
                <input type="text" name="date" value="" v-model="query.moreTotalFee">
              </div>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 120px" clearable v-model="query.status" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 120px" clearable v-model="query.settleStatus" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label></label>
              <el-select style="width: 140px" clearable v-model="query.payType" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value="S"></el-option>
                <el-option label="" value="N"></el-option>
                <el-option label="H5" value="H">H5</el-option>
                <el-option label="" value="B"></el-option>
                <el-option label="" value="Z"></el-option>
              </el-select>
            </li>
            <li class="same">
              <div class="btn btn-primary" @click="search"></div>
            </li>
          </ul>
          <!---->
          <el-table v-loading.body="loading" height="583" style="font-size: 12px;margin:15px 0" :data="records" border>
            <el-table-column type="index" width="62" label="" fixed="left"></el-table-column>
            <el-table-column label="" min-width="112">
              <template scope="scope">
                <span class="td" :data-clipboard-text="records[scope.$index].orderNo" type="text" size="small"
                      style="cursor: pointer" title="">{{records[scope.$index].orderNo|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="112">
              <template scope="scope">
                <span class="td" :data-clipboard-text="records[scope.$index].sn" type="text" size="small"
                      style="cursor: pointer" title="">{{records[scope.$index].sn|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="createTime" :formatter="changeTime" label="" width="162"></el-table-column>
            <el-table-column prop="merchantName" label="" min-width="90"></el-table-column>
            <el-table-column prop="proxyName" label="" min-width="90"></el-table-column>
            <el-table-column prop="proxyName1" label="" min-width="90"></el-table-column>
            <el-table-column prop="tradeAmount" :formatter="changeNum" label="" min-width="90"
                             align="right"></el-table-column>
            <el-table-column prop="payRate" label="" min-width="90" align="right"></el-table-column>
            <el-table-column prop="appId" label="" min-width="85"></el-table-column>
            <el-table-column prop="status" :formatter="changeStatus" label="" min-width="90"></el-table-column>
            <el-table-column prop="settleStatus" :formatter="changeSettleStatus" label=""
                             min-width="90"></el-table-column>
            <el-table-column prop="payType" :formatter="changePayType" label="" min-width="115"></el-table-column>
            <el-table-column prop="payChannelSign" :formatter="changePayChannel" label=""
                             min-width="115"></el-table-column>
            <el-table-column prop="remark" label="" min-width="90"></el-table-column>
            <el-table-column label="" width="90" fixed="right">
              <template scope="scope">
                <router-link :to="{path:'/admin/record/newDealDet',query:{orderNo:records[scope.$index].orderNo}}"
                             type="text" size="small">
                </router-link>
              </template>
            </el-table-column>
          </el-table>
          </el-table>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="query.page"
                           :page-sizes="[10, 20, 50]"
                           :page-size="query.size"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="count">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="babel">
  import Clipboard from "clipboard"
  export default{
    name: 'deal',
    data(){
      return {
        query:{
          page:1,
          size:10,
          orderNo:'',
          merchantName: '',
          startTime: '',
          endTime: '',
          lessTotalFee: '',
          moreTotalFee: '',
          status: '',
          settleStatus:'',
          payType:'',
          proxyName:'',
          proxyName1:''
        },
        date: '',
        records: [],
        count: 0,
        total: 0,
        loading: true,
        url: ''
      }
    },
    created: function () {
      var clipboard = new Clipboard('.td');
      //
      clipboard.on('success', (e) => {
        this.$message({
          showClose: true,
          message: "  " + e.text,
          type: 'success'
        });
      });
      this.getData()
    },
    methods: {
      getData: function () {
        this.loading = true;
        this.$http.post('/admin/queryOrder/orderList',this.$data.query)
          .then(function (res) {
            this.loading = false;
            this.$data.records = res.data.records;
            this.$data.total=res.data.totalPage;
            this.$data.url=res.data.ext;
            this.$data.count = res.data.count;
            for (var i = 0; i < this.records.length; i++) {
              if (this.records[i].payRate != null) {
                this.records[i].payRate = (parseFloat(this.records[i].payRate) * 100).toFixed(2) + '%';
              }
            }
          },function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      },
      //hss
      changeTime: function (row, column) {
        var val = row.createTime;
        if (val == '' || val == null) {
          return ''
        } else {
          val = new Date(val)
          var year = val.getFullYear();
          var month = val.getMonth() + 1;
          var date = val.getDate();
          var hour = val.getHours();
          var minute = val.getMinutes();
          var second = val.getSeconds();

          function tod(a) {
            if (a < 10) {
              a = "0" + a
            }
            return a;
          }

          return year + "-" + tod(month) + "-" + tod(date) + " " + tod(hour) + ":" + tod(minute) + ":" + tod(second);
        }
      },
      changeNum: function (row, column) {
        var val = row.tradeAmount;
        return parseFloat(val).toFixed(2);
      },
      changePayType: function (row, column) {
        var val = row.payType;
        if(val == "S"){
          return ""
        }else if(val == "N"){
          return ""
        }else if(val == "H"){
          return "H5"
        }else if(val == "B"){
          return ""
        }else if(val == "Z"){
          return ""
        }
      },
      changeStatus: function (row, column) {
        var val = row.status;
        if(val == 1){
          return ""
        }else if(val == 3){
          return ""
        }else if(val == 4){
          return ""
        }else if(val == 5){
          return ""
        }else if(val == 6){
          return ""
        }else if(val == 7){
          return ""
        }else if(val == 6){
          return ""
        }
      },
      changeSettleStatus: function (row, column) {
        var val = row.settleStatus;
        if(val == 2){
          return ''
        }else if(val == 1){
          return ''
        }else if(val == 3){
          return ''
        }
      },
      changePayChannel: function (row, column) {
        var val = row.payChannelSign;
        if(val == 101){
          return ''
        }else if(val == 102){
          return ''
        }else if(val == 103){
          return ''
        }
      },
      search(){
        this.$data.query.page = 1;
        this.getData()
      },
      //
      handleSizeChange(val) {
        this.$data.query.page = 1;
        this.$data.query.size = val;
        this.getData()
      },
      //
      handleCurrentChange(val) {
        this.$data.query.page = val;
        this.getData()
      },
    },
    watch: {
      date: function (val, oldVal) {
        if (val[0] != null) {
          for (var j = 0; j < val.length; j++) {
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for (var i = 0, len = ary.length; i < len; i++) {
              if (ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if (j == 0) {
              this.$data.query.startTime = str;
            } else {
              this.$data.query.endTime = str;
            }
          }
        } else {
          this.$data.query.startTime = '';
          this.$data.query.endTime = '';
        }
      }
    },
    filters: {
      changeHide: function (val) {
        if(val!=""&&val!=null){
          val = val.replace(val.substring(3,val.length-6),"");
        }
        return val
      },
    }
  }
</script>

<style scoped lang="less" rel="stylesheet/less">
  ul {
    padding: 0;
  }

  .same {
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
  .btn{
    font-size: 12px;
  }

  .price {
    display: inline-block;
    width: 210px;
    height: 30px;
    border-radius: 4px;
    border-color: #bfcbd9;
    position: relative;
    top: 6px;
    input {
      border: none;
      display: inline-block;
      width: 45%;
      height: 25px;
      position: relative;
      top: -3px;
    }
  }

  .price:hover {
    border-color: #20a0ff;
  }

</style>



