<template>
  <div>
    <div class="col-md-12">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title"></h3>
        </div>
        <div class="box-body">
          <!---->
          <ul>
            <li class="same">
              <label></label>
              <el-input style="width: 120px" v-model="query.mobile" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label></label>
              <el-input style="width: 120px" v-model="query.markCode" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label></label>
              <el-input style="width: 120px" v-model="query.markCode" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label></label>
              <el-input style="width: 120px" v-model="query.markCode" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label></label>
              <el-input style="width: 120px" v-model="query.markCode" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 120px" clearable v-model="query.sysType" size="small" >
                <el-option label="" value=""></el-option>
                <el-option label="" value="hsy"></el-option>
                <el-option label="" value="hss"></el-option>
                <el-option label="" value="hss"></el-option>
                <el-option label="" value="hss"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label></label>
              <el-select style="width: 120px" clearable v-model="query.sysType" size="small" >
                <el-option label="" value=""></el-option>
                <el-option label="" value="hsy"></el-option>
                <el-option label="" value="hss"></el-option>
                <el-option label="" value="hss"></el-option>
                <el-option label="" value="hss"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 120px" clearable v-model="query.sysType" size="small" >
                <el-option label="" value=""></el-option>
                <el-option label="" value="hsy"></el-option>
                <el-option label="" value="hss"></el-option>
                <el-option label="" value="hss"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-date-picker
                v-model="value7"
                type="daterange"
                align="right"
                placeholder=""
                :picker-options="pickerOptions2" size="small">
              </el-date-picker>
            </li>
            <li class="same">
              <label></label>
              <el-input style="width: 120px" v-model="query.markCode" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <div class="btn btn-primary" @click="search"></div>
            </li>
          </ul>
          <!---->
          <el-table v-loading.body="loading"  style="font-size: 12px;margin:15px 0" :data="records" border >
            <el-table-column label="">
              <template scope="scope">
                <span to="">{{(query.page-1)*10+(index+1)}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
            <el-table-column prop="level" label="" ></el-table-column>
          </el-table>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @current-change="handleCurrentChange" :current-page="currentPage" layout="total, prev, pager, next, jumper" :total="count">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>
<script lang="babel">
  export default{
    name: 'tradeQuery',
    data(){
      return{
        pickerOptions2: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value7:'',
        loading:true,
        records:[],
        count:0,
        total:0,
        query:{
          pageNo:1,
          pageSize:10,
          mobile:'',
          name:"",
          markCode:"",  //
          sysType:"",
          districtCode:""
        },
        currentPage: 1,
      }
    },
    created: function () {

    },
    methods: {
      search: function () {
        this.$data.query.pageNo = 1;
        this.$data.records = '';
        this.$data.loading = true;

      },
      //
      handleCurrentChange(val) {
        this.$data.query.pageNo = val;
        this.$data.records = '';
        this.$data.loading = true;

      }
    },
  }
</script>
<style scoped lang="less">
  .btn{
    font-size: 12px;
  }
  ul{
    padding: 0;
  }
  .same{
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
</style>



