<template lang="html">
  <div id="dealList">
    <!--<div style="padding: 8px 30px; background: rgb(243, 156, 18); z-index: 999999; font-size: 22px; font-weight: 600;margin-bottom: 15px;    color: #fff;"></div>-->
    <div class="box-header with-border" style="margin: 0 0 0 3px;">
      <h3 class="box-title" style="border-left: 3px solid #e4e0e0;padding-left: 10px;"></h3>
    </div>
    <div style="margin: 0 15px">
      <div class="box box-primary">
        <p class="lead"> </p>
        <div class="table-responsive">
          <table class="table">
            <tbody>
            <tr>
              <th style="text-align: right">:</th>
              <td></td>
              <!--<td>{{record.merchantType|changeMerchantType}}</td>-->
              <th style="text-align: right">:</th>
              <td>{{record.markCode}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.merchantName}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.mobile}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.bankNo}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.reserveMobile}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.proxyName}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.proxyName1}}</td>
              <th style="text-align: right">:</th>
              <td></td>
            </tr>
            </tbody></table>
        </div>
      </div>
      <div class="box box-primary">
        <p class="lead"> </p>
        <div class="table-responsive">
          <table class="table">
            <tbody>
            <tr>
              <th style="text-align: right">:</th>
              <td></td>
              <th style="text-align: right">:</th>
              <td></td>
              <th style="text-align: right">:</th>
              <td>{{record.orderNo}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.tradeAmount}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.payRate}}</td>
              <th style="text-align: right"></th>
              <td>{{record.sn}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.payChannelSign|changePayChannel}}</td>
              <th style="text-align: right">:</th>
              <td></td>
              <th style="text-align: right">:</th>
              <td>{{record.poundage}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.payType|changePayType}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.tradeType|changeTradeType}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.status|changeStatus}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.settleStatus|changeSettleStatus}}</td>
              <th style="text-align: right">:</th>
              <td></td>
              <th style="text-align: right">:</th>
              <td>{{record.createTime|changeTime}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.paySuccessTime|changeTime}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.settleTime|changeTime}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.successSettleTime|changeTime}}</td>
            </tr>
            <tr>
              <th style="text-align: right">:</th>
              <td>{{record.goodsName}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.goodsDescribe}}</td>
              <th style="text-align: right">:</th>
              <td>{{record.remark}}</td>
            </tr>
            </tbody></table>
        </div>
      </div>
      <div class="box box-primary">
        <p class="lead"> </p>
        <div class="table-responsive">
          <table class="table">
            <tbody>
            </tbody>
          </table>
        </div>
      </div>
      <div class="box box-primary">
        <p class="lead"> </p>
        <div class="table-responsive">
          <table class="table">
            <tbody>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <message></message>
  </div>
  </div>
</template>

<script lang="babel">
  import Message from '../Message.vue'
  export default {
    name: 'dealList',
    components: {
      Message
    },
    data: function () {
      return {
        record:{}
      }
    },
    created: function(){
      this.$http.post('/admin/queryOrder/orderListAll',{orderNo:this.$route.query.orderNo})
        .then(function (res) {
          this.$data.record = res.data;
        },function (err) {
          console.log(err)
        })
    },
    filters: {
      changeMerchantType: function (val) {
        if(val==0){
          return ""
        }else if(val==1){
          return ""
        }
      },
      changeTime: function (val) {
        if(val==''||val==null){
          return ''
        }else {
          val = new Date(val)
          var year=val.getFullYear();
          var month=val.getMonth()+1;
          var date=val.getDate();
          var hour=val.getHours();
          var minute=val.getMinutes();
          var second=val.getSeconds();
          function tod(a) {
            if(a<10){
              a = "0"+a
            }
            return a;
          }
          return year+"-"+tod(month)+"-"+tod(date)+" "+tod(hour)+":"+tod(minute)+":"+tod(second);
        }
      },
      changePayType: function (val) {
        if(val == "S"){
          return ""
        }else if(val == "N"){
          return ""
        }else if(val == "H"){
          return "H5"
        }else if(val == "B"){
          return ""
        }else if(val == "Z"){
          return ""
        }
      },
      changeStatus: function (val) {
        if(val == 1){
          return ""
        }else if(val == 3){
          return ""
        }else if(val == 4){
          return ""
        }else if(val == 5){
          return ""
        }else if(val == 6){
          return ""
        }else if(val == 7){
          return ""
        }else if(val == 6){
          return ""
        }
      },
      changeSettleStatus: function (val) {
        if(val == 2){
          return ''
        }else if(val == 1){
          return ''
        }else if(val == 3){
          return ''
        }
      },
      changePayChannel: function (val) {
        if(val == 101){
          return ''
        }else if(val == 102){
          return ''
        }else if(val == 103){
          return ''
        }
      },
      changeTradeType: function (val) {
        if(val == 1){
          return ""
        }else if(val == 2){
          return ""
        }else if(val ==3){
          return ""
        }
      },
      changeName: function (val) {
        if(val==null){
          return ""
        }else {
          return val
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .flexItem(@val) {
    box-flex: @val;
    -webkit-box-flex: @val;
    -webkit-flex: @val;
    -ms-flex: @val;
    flex: @val;
  }

  .flexBox {
    display: box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
  }
  h1, h2 {
    font-weight: normal;
    color: #337ab7;
    font-weight: bold;
    border-bottom: 2px solid #ccc;
    padding-bottom: 10px;
  }

  ul {
    list-style-type: none;
    padding: 0;
    background: #fff;
    position: relative;
    left: 70px;
    width: 300px;
  }

  li {
    /*display: inline-block;*/
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

</style>



